.qarr-dashboard .chart-explorer-container {
  position: relative;
  height: 130px;
  width: 70%;
  padding-bottom: 80px;
  margin-bottom: 25px;
}

.qarr-dashboard .chart-explorer-container .chart-explorer {
  height: 100%;
  padding-bottom: 80px;
  border-radius: 5px;
}

.qarr-dashboard .chart-explorer-container .chart-container {
  padding: 0 20px;
}

.qarr-dashboard .chart-explorer-container .chart-container, .qarr-dashboard .chart-explorer-container .chart {
  height: 130px;
}

.qarr-dashboard .chart-explorer-container .chart-header {
  display: flex;
  align-items: center;
  padding: 20px 20px 30px 20px;
  box-sizing: border-box;
}

.qarr-dashboard .chart-explorer-container .timeline-wrapper {
  margin-left: auto;
}

.qarr-dashboard .chart-explorer-container .timeline-wrapper button {
  -webkit-appearance: none;
  border: 1px solid #E9EFF4;
  outline: none;
  padding: 10px 30px;
  cursor: pointer;
}

.qarr-dashboard .chart-explorer-container .timeline-wrapper button:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.qarr-dashboard .chart-explorer-container .timeline-wrapper button:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.qarr-dashboard .chart-explorer-container .timeline-wrapper button.active {
  background: #E9EFF4;
  font-weight: 800;
  color: #474747;
}

.qarr-dashboard .chart-explorer-container .loader {
  position: absolute;
  top: 65%;
  left: 50%;
  margin-top: -10px;
  margin-left: -10px;
}

.qarr-dashboard .chart-explorer-container .total {
  display: flex;
  font-size: 12px;
  color: #a5a6a8;
  text-transform: uppercase;
}

.qarr-dashboard .chart-explorer-container .total p {
  color: #a5a6a8 !important;
  letter-spacing: .1em;
}

.qarr-dashboard .chart-explorer-container .total .total-value-wrapper {
  margin-right: 5px;
  letter-spacing: .1em;
}

.qarr-dashboard .chart-explorer-container .tooltip {
  text-align: center;
  background: white;
  box-shadow: none;
}

.qarr-dashboard .chart-explorer-container .tooltip .x-value {
  font-size: 10px;
  font-weight: 600;
  color: #a5a6a8;
}

.qarr-dashboard .chart-explorer-container .tooltip .y-value {
  font-size: 18px;
  font-weight: 800;
  color: #4da1ff;
}

.qarr-dashboard .chart-explorer-container .grid-line .tick line {
  stroke: #e2eaf1 !important;
}

.qarr-dashboard .chart-explorer-container .tick text {
  stroke: none !important;
  fill: #a5a6a8 !important;
}

.qarr-dashboard .chart-explorer-container .axis path {
  stroke: #E9EFF4 !important;
}

.qarr-dashboard .chart-explorer-container .plots .plot {
  fill: #4da1ff !important;
}

.qarr-dashboard .chart-explorer-container .chart-line path {
  stroke: #4da1ff !important;
}

.qarr-dashboard .chart-explorer-container .chart-area path {
  fill: #4da1ff !important;
}

@media only screen and (max-width: 767px) {
  .qarr-dashboard .chart-explorer-container {
    height: 150px;
  }
  .qarr-dashboard .chart-explorer-container .chart-header {
    display: block;
  }
  .qarr-dashboard .chart-explorer-container .timeline-wrapper button {
    width: 50%;
  }
  .qarr-dashboard .chart-explorer-container .total {
    margin-bottom: 15px;
  }
}



